import React from 'react'
import {Card,CardTitle,CardText,IconButton,CardActions,Button,CardMenu} from 'react-mdl'

function Unity() {
    return (
        <div>
            <Card shadow={1}  style={{width: '400px', margin: 'auto'}}>
        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://unity.com/logo-unity-web.png) center / cover'}}>Unity Project #1</CardTitle>
        <CardText>
           An FPS survival game.
        </CardText>
        <CardActions border>
            <Button colored>Comming Soon</Button>
        </CardActions>
        <CardMenu style={{color: '#fff'}}>
            <IconButton name="share" />
        </CardMenu>
    </Card>
        </div>
    )
}

export default Unity
